import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

// const Foo = () => import('../components/foo.vue')
const doubanfilm = () => import('../components/douban/Doubanfilm.vue')

export default new Router({
  routes: [
    {
      path: '/',
      name: 'doubanfilm',
      component: doubanfilm
    },
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    // {
    //   path: '/foo',
    //   name: 'foo',
    //   component: Foo
    // },
  ]
})



